import React from 'react';
import DialogItem from './DialogItem/DialogItem';
import classes from './Dialogs.module.css';
import Message from './Messages/Messages';
import { AddMsgForm } from '../common/MyForms/MyForms';


const Dialogs = ({sendMsg, dialogItems, messages, }) => {
  const onSendMsg = (values) => {
    sendMsg(values.chatTextArea);
  };

  return (
    <div className={`dialogs-content ${classes.dialogsContent}`}>
      <h2>Dialogs</h2>

      <div className={classes.dialogs}>
        <div className={classes.dialog}>
          <ul className={classes.dialogsList}>
            {dialogItems.map((d, index) => <DialogItem id={d.id} name={d.name} actived={d.actived} key={index} />)}
          </ul>
        </div>

        <div className={classes.chat}>
          <div className={classes.chatField}>
            {messages.map((m, index) => <Message name={m.name} text={m.text} responsed={m.responsedMsg} key={index} />)}
          </div>

          <AddMsgForm onSubmit={onSendMsg} classes={classes}/>
        </div>
      </div>
    </div>
  )
};

export default Dialogs;