import classes  from './Messages.module.css';

const Message = ({responsed, name, text}) => {
  const responedMsg = responsed ? `${classes.chatMsg} ${classes.responsed}` : classes.chatMsg;

  return (
    <div className={responedMsg}>
      <div className={classes.chatUser}>
        <div className='userAvatar'></div>
        {name}
      </div>
      
      <div className={classes.chatMsgField}>
        {text}
      </div> 
    </div>
  );
};

export default Message;